<template>
	<v-treeview
		open-all
		:items="cameraSources"
		item-text="deviceName"
		item-value="deviceId"
		label="Video Source"
		ref="videoSource"
	>
	</v-treeview>
</template>

<script>
export default {
	name: 'CameraSelection',

	data: function () {
		return {
			cameraSources: [
				{
					deviceName: 'Camera disabled',
					deviceId: 0,
				}
			],
		}
	},

	//on page load
	mounted() {
		this.getCameraSources()
	},

	methods: {
		async getCameraSources() {
			await navigator.mediaDevices
				.enumerateDevices()
				.then((deviceInfos) => {
					console.log(deviceInfos)
				})
				.catch(this.handleError);
		}
	}



}
</script>